import React, { Component } from 'react';
import Rate from '../components/Rate.js'
import Demo from '../components/Demo.js'
class PageRate extends Component{
  state = {
    current : 0
  }
  render(){
    const { current } = this.state;
    return(
      <div>
        <h1>PageRate</h1>
        <h2 style={{textAlign:'left'}}>Rate</h2>
        <br/>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <h3 style={{textAlign:'left'}}></h3>
        <br/>
        <ul style={{textAlign:'left'}}>
          <li></li>
          <br/>
          <li></li>
          <br/>
        </ul>
        <h2 style={{textAlign:'left'}}></h2>
        <br/>
        <hr/>

        <Demo title='' desc='' instance={<Rate/>}/>
        <Demo title='' desc='' instance={
          <div>
          <Rate change={ ( current )=>{
            this.setState( {current} );
          }  }/>
          <span>{current}stars</span>
          </div>
        }/>
        <Demo title='' desc='' instance={<Rate allowHalf />}/>
        <Demo title='' desc='' instance={
          <div>
          <Rate allowHalf/>
          <Rate text='A' allowHalf/>
          <Rate text='' allowHalf/>
          </div>
        }/>
      </div>
    )
  }
}
export default PageRate;



