import React, { Component } from 'react';
import Button from '../components/Button.js';
import Notification from '../components/Notification.js';
import Demo from '../components/Demo.js';


console.log( Notification );


class PageNotification extends Component{
  state = {
    show : false,     //Notification
  }
  onClick(){
    Notification.open(
      {
        title:'Notification Title',
        duration:  4500,
        message:  'This is the content of the notification. This is the content of the notification. This is the content of the notification.'
      }
    );
  }
  clearDuration(){
    Notification.open(
      {
        title:'Notification Title',
        message:'This is the content of the notification. This is the content of the notification. This is the content of the notification',
        duration:  0,
      }
    )
  }
  selfName(){
    Notification.open(
      {
        title:'Notification Title',
        message:'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
        duration: 4500,
        btn:'Confirm',
      }
    )
  }
  Success(){
    Notification.open(
      {
        title:'Notification Title',
        message:'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
        duration: 4500,
        type:'success',
      }
    )
  }
  Info(){
    Notification.open(
      {
        title:'Notification Title',
        message:'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
        duration: 4500,
        type:'info',
      }
    )
  }
  Warning(){
    Notification.open(
      {
        title:'Notification Title',
        message:'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
        duration: 4500,
        type:'warning',
      }
    )
  }
  Error(){
    Notification.open(
      {
        title:'Notification Title',
        message:'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
        duration: 4500,
        type:'error',
      }
    )
  }
  topRight(){
    Notification.open(
      {
        title:'Notification Title',
        message:'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
        duration: 0,
        position: 'topRight'
      }
    )
  }
  topLeft(){
    Notification.open(
      {
        title:'Notification Title',
        message:'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
        duration: 0,
        position: 'topLeft'
      }
    )
  }
  bottomRight(){
    Notification.open(
      {
        title:'Notification Title',
        message:'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
        duration: 0,
        position: 'bottomRight'
      }
    )
  }
  bottomLeft(){
    Notification.open(
      {
        title:'Notification Title',
        message:'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
        duration: 0,
        position: 'bottomLeft'
      }
    )
  }
  render(){
    let {show} = this.state;
    return(
      <div>
        <h1>PageNotification</h1>
        <h2 style={{textAlign:'left'}}>Notification</h2>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <h3 style={{textAlign:'left'}}></h3>
        <br/>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <ul style={{textAlign:'left'}}>
          <li></li>
          <br/>
          <li></li>
          <br/>
          <li></li>
          <br/>
        </ul>
        <br/>
        <h2 style={{textAlign:'left'}}></h2>
        <br/>
        <hr/>
        <Demo title='' desc='4.5 ' instance={
          <div>
            <Button type='normal' text='Open the notification box' onClick={()=>{this.onClick()}}/>
          </div>
        }/>
        <Demo title='' desc='4.5s 0 ' instance={
          <Button type='normal' text='Open the notification box' onClick={()=>{this.clearDuration()}}/>
        }/>
        <Demo title='' desc='' instance={
          <Button type='normal' text='Open the notification box' onClick={()=>{this.selfName()}}/>
        }/>
        <Demo title='' desc='' instance={
          <div>
            <Button text='Success' onClick={()=>{this.Success()}}/>
            <Button text='Info' onClick={()=>{this.Info()}}/>
            <Button text='Warning' onClick={()=>{this.Warning()}}/>
            <Button text='Error' onClick={()=>{this.Error()}}/>
          </div>
        }/>
        <Demo title='' desc=' style  className ' instance={
          <Button type='normal' text='Open the notification box' onClick={()=>{this.changeStyle()}}/>
        }/>
        <Demo title='' desc='' instance={
          <div>
            <Button text='topRight' onClick={()=>{this.topRight()}}/>
            <Button text='topLeft' onClick={()=>{this.topLeft()}}/>
            <Button text='bottomRight' onClick={()=>{this.bottomRight()}}/>
            <Button text='bottomLeft' onClick={()=>{this.bottomLeft()}}/>
          </div>
        }/>
      </div>
    )
  }
  changeStyle(){
    Notification.open(
      {
        title:'Notification Title',
        message:'This is the content of the notification. This is the content of the notification. This is the content of the notification',
        duration:  4500,
        style: {
          width: 600,
          marginLeft: 335 - 600,
        },
      }
    )
  }
};
export default PageNotification;



