import React, { Component } from 'react';
import {Spin,Icon} from '../components/Spin.js';
import Demo from '../components/Demo.js';
import Alert from '../components/Alert.js';
import Switch from '../components/Switch.js';

class PageSpin extends Component{
  state = {
    loading : false
  }
  toggle(loading){
    this.setState({loading : !loading})
  }
  render(){
    let {loading} = this.state;
    const antIcon = <Icon type="loading" style={{ fontSize: 24 }}/>;
    return(
      <div>
        <h1>PageSpin</h1>
        <h2 style={{textAlign:'left'}}>Spin</h2>
        <br/>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <h3 style={{textAlign:'left'}}></h3>
        <br/>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <h2 style={{textAlign:'left'}}></h2>
        <br/>
        <hr/>
        <Demo title='' desc=' loading ' instance={<Spin />}/>
        <Demo title='' desc='' instance={
          <div>
            <Spin size="small" />
            <Spin />
            <Spin size="large" />
          </div>
        }/>
        <Demo title='' desc='' instance={
          <div className="example">
            <Spin />
          </div>
        }/>
        <Demo title='' desc='' instance={
          <div style={{position:'relative'}}>
            <Spin tip="Loading..."/>
            <Alert
              message="Alert message title"
              description="Further details about the context of this alert."
              type="info"
            />
          </div>
        }/>
        <Demo title='' desc='' instance={
          <Spin indicator={antIcon} />
        }/>
      </div>
    )
  }
}

export default PageSpin;



