import React, { Component } from 'react';
import Pagination from '../components/Pagination.js';
import Demo from '../components/Demo.js';
class PagePagination extends Component{
  render(){
    return(
      <div>
        <h1>PagePagination</h1>
        <h2 style={{textAlign:'left'}}>Pagination</h2>
        <br/>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <h3 style={{textAlign:'left'}}></h3>
        <br/>
        <ul style={{textAlign:'left'}}>
          <li>/</li>
          <br/>
          <li></li>
          <br/>
        </ul>
        <h2 style={{textAlign:'left'}}></h2>
        <br/>
        <hr/>
        <Demo title='' desc='total' instance={<Pagination  total={5}/>}/>
        <Demo title='' desc='' instance={<Pagination  total={5} simple/>}/>
        <Demo title='' desc='' instance={<Pagination  total={5}/>}/>
        <Demo title='' desc='typeword' instance={<Pagination  total={5} type='word'/>}/>
        <Demo title='' desc=' showTotal ' instance={<Pagination  total={5} showTotal/>}/>
        <Pagination  total={50}/>
      </div>
    )
  }
}
export default PagePagination;



