import React, { Component } from 'react';
import Radio from '../components/Radio.js'
import Demo from '../components/Demo.js'
class PageRadio extends Component{
  render(){
    return(
      <div>
        <h1>PageRadio</h1>
        <h2 style={{textAlign:'left'}}>Radio</h2>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <h3 style={{textAlign:'left'}}></h3>
        <br/>
        <ul style={{textAlign:'left'}}>
          <li></li>
          <br/>
          <li> Select Radio </li>
          <br/>
        </ul>
        <h2 style={{textAlign:'left'}}></h2>
        <br/>
        <hr/>
        <Demo title='' desc='Radiotyperadio' instance={
          <Radio type="radio" text="Radio"/>
        }/>
        <Demo title='' desc='Radiodisabled' instance={
          <div>
            <Radio type="radio" disabled text="disabled"/>
            <Radio type="radio" disabled text="disabled"/>
          </div>
        }/>
        <Demo title='' desc=' Radio ' instance={
          <div>
            <Radio type="radio" text="A" value="1"/>
            <Radio type="radio" text="B" value="2"/>
            <Radio type="radio" text="C" value="3"/>
            <Radio type="radio" text="D" value="4"/>
          </div>
        }/>
      </div>
    )
  }
}
export default PageRadio;



