import Checkbox from '../components/Checkbox.js';
import React, { Component } from 'react';
import Demo from '../components/Demo.js';
class PageCheckbox extends Component{
  render(){
    return(
      <div>
        <h1>PageCheckbox</h1>
        <h2 style={{textAlign: 'left'}}>Checkbox</h2>
        <p style={{textAlign: 'left'}}></p>
        <br/>
        <h3 style={{textAlign: 'left'}}></h3>
        <br/>
        <ul style={{textAlign: 'left'}}>
          <li></li>
          <br/>
          <li> switch  switch  checkbox </li>
          <br/>
        </ul>
        <h2 style={{textAlign: 'left'}}></h2>
        <br/>
        <hr/>
        <Demo title='' desc=' checkbox' instance={<Checkbox title='checkbox'/>}/>
        <Demo title='' desc='checkbox disabledchecked' instance={
          <div>
            <Checkbox disabled/>
            <br/>
            <Checkbox disabled checked/>
          </div>
        }/>
        <Demo title='' desc='' instance={
          <div>
          <Checkbox title='Apple'/>
          <Checkbox title='Pear'/>
          <Checkbox title='Orange'/>
          <br/>
          <br/>
          <Checkbox title='Apple'/>
          <Checkbox title='Pear'/>
          <Checkbox title='Orange'/>
          <br/>
          <br/>
          <Checkbox title='Apple' disabled checked/>
          <Checkbox title='Pear' disabled checked/>
          <Checkbox title='Orange' disabled checked/>
          </div>
        }/>
        <Demo title='' desc='' instance={
          <div>
          <Checkbox title='Apple'/>
          <Checkbox title='Pear'/>
          <Checkbox title='Orange'/>
          <br/>
          <br/>
          <Checkbox title='Apple'/>
          <Checkbox title='Pear'/>
          <Checkbox title='Orange'/>
          <br/>
          <br/>
          <Checkbox title='Apple' disabled checked/>
          <Checkbox title='Pear' disabled checked/>
          <Checkbox title='Orange' disabled checked/>
          </div>
        }/>
        <Demo title='' desc='' instance={
          <div>
          <Checkbox title='Check all'/>
          <hr/>
          <Checkbox title='Apple'/>
          <Checkbox title='Pear'/>
          <Checkbox title='Orange'/>
          </div>
        }/>
        <Demo title='' desc='' instance={
          <div>
          <Checkbox title='A'/>
          <Checkbox title='B'/>
          <Checkbox title='C'/>
          <Checkbox title='D'/>
          <Checkbox title='E'/>
          </div>
        }/>
      </div>
    )
  }
}
export default PageCheckbox



