import React, { Component } from 'react';
import Popover from '../components/Popover.js';
import Button from '../components/Button.js';
import Demo from '../components/Demo.js';
class PagePopover extends Component{
  render(){
    const content = (
      <div className='popover-con'>
        <p>Content</p>
        <p>Content</p>
      </div>
    );
    const content2 = (
      <div className='popover-con'>
        <p style={{color:'#189ff0',cursor:'pointer'}}>close</p>
      </div>
    );
    const text = <span>Title</span>;
    return(
      <div>
        <h1>PagePopover</h1>
        <h3 style={{textAlign:'left'}}>ButtonButton.js</h3>
        <h2 style={{textAlign:'left'}}>Popover</h2>
        <p style={{textAlign:'left'}}>/</p>
        <br/>
        <h3 style={{textAlign:'left'}}></h3>
        <br/>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <p style={{textAlign:'left'}}> Tooltip </p>
        <br/>
        <h2 style={{textAlign:'left'}}></h2>
        <br/>
        <hr/>
        <Demo title='' desc='' instance={
          <div>
          <Popover content={content} title="Title" trigger='hover'>
            <Button type="normal" text='Hover me'/>
          </Popover>
          </div>
        }/>
        <Demo title='' desc='' instance={
          <div>
          <Popover content={content} title="Title" trigger='hover'>
            <Button text='Hover me'/>
          </Popover>
          {
            // <Popover content={content} title="Title" trigger="focus">
            //   <Button text='Focus me'/>
            // </Popover>
          }
          <Popover content={content} title="Title" trigger="click">
            <Button text='Click me'/>
          </Popover>
          </div>
        }/>
        <Demo title='' desc=' arrowPointAtCenter ' instance={
          <div>
          <Popover placement="topLeft" title={text} content={content} trigger='hover'>
            <Button text='Align edge / '/>
          </Popover>
          <Popover placement="topLeft" title={text} content={content} arrowPointAtCenter trigger='hover'>
            <Button text='Arrow points to center / '/>
          </Popover>
          </div>
        }/>
      </div>
    )
  }
}
export default PagePopover;



