import React, { Component } from 'react';
import Card from '../components/Card.js'
import Demo from '../components/Demo.js'
class PageCard extends Component{
  render(){
    return(
      <div>
        <h1>PageCard</h1>
        <h2 style={{textAlign:'left'}}>Card</h2>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <h3 style={{textAlign:'left'}}></h3>
        <br/>
        <p style={{textAlign:'left'}}></p>
        <br/>
        <h2 style={{textAlign:'left'}}></h2>
        <br/>
        <hr/>
        <Demo title='' desc='titleextra' instance={
          <div>
            <Card title="Card title" extra={<a href="#">More</a>}>
              <p>Card content</p>
              <p>Card content</p>
              <p>Card content</p>
            </Card>
          </div>
        }/>
        <Demo title='' desc='' instance={
          <div style={{ background: '#ECECEC', padding: '30px' }}>
           <Card title="Card title" bordered={false}>
             <p>Card content</p>
             <p>Card content</p>
             <p>Card content</p>
           </Card>
         </div>
        }/>
        <Demo title='' desc='' instance={
          <div>
            <Card style={{ width: 300 }}>
             <p>Card content</p>
             <p>Card content</p>
             <p>Card content</p>
            </Card>
          </div>
        }/>
      </div>
    )
  }
}
export default PageCard



